import {NgModule, Pipe, PipeTransform} from '@angular/core';
import {SelectOption} from './select.model';
import {CommonModule} from '@angular/common';
import {Observable, of} from 'rxjs';
import {map} from 'rxjs/operators';

@Pipe({
  name: 'searchFilter'
})

/**
 * Search filter by name and badgeText
 */

export class AdaptSelectSearchFilter implements PipeTransform {

  transform(options: SelectOption[], args: string): SelectOption[] {
    return options.filter((option: SelectOption) => {
      return option.name && option.name.toLowerCase().indexOf((args || '').toLowerCase()) > -1
        || option.badgeText && option.badgeText.toLowerCase().indexOf((args || '').toLowerCase()) > -1;
    });
  }
}

@Pipe({
  name: 'searchFilterAsync'
})

/**
 * Search filter by name
 */

export class AdaptSelectSearchFilterAsync implements PipeTransform {

  transform(options: SelectOption[], query: string): Observable<SelectOption[]> {
    return of(options).pipe(
      map(data => {
        if (!query) {
          return data;
        }

        return data.filter((option: SelectOption) => option.name.toLowerCase().indexOf((query || '').toLowerCase()) > -1);
      })
    );
  }
}


@NgModule({
  imports: [
    CommonModule
  ],
  declarations: [
    AdaptSelectSearchFilter,
    AdaptSelectSearchFilterAsync
  ],
  exports: [
    AdaptSelectSearchFilter,
    AdaptSelectSearchFilterAsync
  ]
})
export class AdaptSelectSearchFilterModule {
}
